import { getQuery, parseQuery } from 'api/queries/ocpQuery';
import BreakdownBase from 'pages/details/components/breakdown/breakdownBase';
import { getGroupById, getGroupByValue, } from 'pages/details/components/utils/groupBy';
import React from 'react';
import { translate } from 'react-i18next';
import { connect } from 'react-redux';
import { createMapStateToProps } from 'store/common';
import { reportActions, reportSelectors } from 'store/reports';
import { CostOverview } from './costOverview';
import { HistoricalData } from './historicalData';
const detailsURL = '/details/ocp';
const reportType = "cost" /* cost */;
const reportPathsType = "ocp" /* ocp */;
const mapStateToProps = createMapStateToProps(state => {
    const queryFromRoute = parseQuery(location.search);
    const query = queryFromRoute;
    const queryString = getQuery(query);
    const report = reportSelectors.selectReport(state, reportPathsType, reportType, queryString);
    const reportError = reportSelectors.selectReportError(state, reportPathsType, reportType, queryString);
    const reportFetchStatus = reportSelectors.selectReportFetchStatus(state, reportPathsType, reportType, queryString);
    const filterBy = getGroupByValue(query);
    const groupBy = getGroupById(query);
    return {
        costOverviewComponent: (React.createElement(CostOverview, { filterBy: filterBy, groupBy: groupBy, report: report })),
        detailsURL,
        filterBy,
        groupBy,
        historicalDataComponent: (React.createElement(HistoricalData, { filterBy: filterBy, groupBy: groupBy })),
        query,
        queryString,
        report,
        reportError,
        reportFetchStatus,
        reportType,
        reportPathsType,
        title: filterBy,
    };
});
const mapDispatchToProps = {
    fetchReport: reportActions.fetchReport,
};
const OcpBreakdown = translate()(connect(mapStateToProps, mapDispatchToProps)(BreakdownBase));
export default OcpBreakdown;
